:root {
  font-size: 0.82353em;
  --green: #63D1BB;
  --black: #000000;
  --gray: #ECECEC;
  --gray-back: #F9F9F9;
  --white: #fff;
}
@media (min-width: 768px) {
  :root {
    font-size: 0.884em;
  }
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.container {
  width: auto;
  padding: 0 2.14285714em;
}
@media (min-width: 768px) {
  .container {
    padding: 0 3.33333333em;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0;
    max-width: 1052px;
    margin: 0 auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  font-family: "Open Sans", Arial, sans-serif;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  grid-template-areas: "header" "main" "footer";
}
.page-body__header {
  background-color: var(--white);
  grid-area: header;
}
.page-body__main {
  grid-area: main;
}
.page-body__footer {
  grid-area: footer;
}
.main-header {
  position: relative;
  background-color: var(--white);
}
@media (min-width: 768px) {
  .main-header::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: var(--gray);
    bottom: 0;
  }
}
.menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 1.21428571rem;
  display: none;
}
@media (min-width: 768px) {
  .menu {
    font-size: 100%;
  }
}
.menu__item {
  border-bottom: 1px solid var(--gray);
}
@media (min-width: 768px) {
  .menu__item {
    border-bottom: none;
  }
}
.menu__link {
  text-align: center;
  display: block;
  text-decoration: none;
  color: var(--black);
  padding: 2.07142857rem 0 1.92857143rem 0;
  font-weight: bold;
}
@media (min-width: 768px) {
  .menu__link {
    text-align: start;
    padding: 0;
  }
  .menu__link--end {
    text-align: end;
  }
}
.navigation__info {
  display: none;
}
@media (min-width: 768px) {
  .navigation {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr auto;
    grid-template-areas: "logo user" "main parag";
    align-items: center;
  }
  .navigation__logo-burger {
    grid-area: logo;
  }
  .navigation__main-menu {
    grid-area: main;
    position: absolute;
    top: 20px;
  }
  .navigation__user-menu {
    grid-area: user;
  }
  .navigation__info {
    grid-area: parag;
    display: block;
    margin: 0;
    position: absolute;
    top: 20px;
    right: 0;
    margin-left: auto;
  }
}
@media (min-width: 768px) and (min-width: 1150px) {
  .navigation {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr auto;
    grid-template-areas: "main logo user" "events . parag";
  }
  .navigation__events-menu {
    grid-area: events;
    position: absolute;
    top: 20px;
  }
  .navigation__main-menu {
    position: static;
  }
}
@media (min-width: 768px) {
  .main-menu {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
  }
  .main-menu__item {
    flex-grow: 1;
  }
}
@media (min-width: 768px) {
  .main-menu {
    justify-content: space-between;
  }
  .main-menu__item {
    flex-grow: 0;
  }
}
@media (min-width: 768px) {
  .user-menu {
    display: grid;
    grid-template-columns: 1fr auto;
    align-items: center;
  }
  .user-menu__item {
    flex-grow: 1;
  }
  .user-menu__span {
    display: none;
  }
  .user-menu__search {
    position: relative;
    height: 88px;
    border-left: 2px solid var(--gray);
    border-right: 2px solid var(--gray);
  }
  .user-menu__icon-search {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .user-menu__link-basket {
    padding: 35px 0;
  }
  .user-menu__basket {
    margin-right: 31px;
    margin-left: 33px;
  }
}
.events-menu {
  display: none;
}
@media (min-width: 1150px) {
  .events-menu {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 65px;
  }
}
.logo-burger {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 1.57142857em 0;
  position: relative;
  border-bottom: 1px solid var(--gray);
}
@media (min-width: 768px) {
  .logo-burger {
    display: block;
    padding: 1.46666667em 0 1.33333333em 0;
    border-bottom: none;
  }
}
@media (min-width: 1150px) {
  .logo-burger {
    padding: 0;
  }
}
.logo-burger__button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}
.logo-burger__button::before {
  content: url("../img/icons/menu-open.svg");
  display: block;
  width: 20px;
  height: 14px;
}
@media (min-width: 768px) {
  .logo-burger__button {
    display: none;
  }
}
.logo-burger__main-link {
  display: block;
  width: 86px;
  height: 35px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .logo-burger__main-link {
    margin: 0;
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .logo-burger__main-link {
    width: 149px;
    height: 72px;
    margin: 0 auto;
  }
}
.promo {
  background-color: var(--gray-back);
}
.promo__title {
  margin: 0 auto;
  text-align: center;
  font-size: 1.92857143rem;
  padding: 0 2.14285714rem;
  line-height: 30px;
  margin-bottom: auto;
}
.promo__container {
  display: flex;
  flex-direction: column;
  padding: 0;
  padding-top: 6.5rem;
  min-height: 489px;
  box-sizing: border-box;
  background-image: url(../img/back-image-mobile.jpg);
  background-size: 320px 373px;
  background-position: center -15%;
  background-repeat: no-repeat;
  margin-bottom: 31px;
}
@media (min-width: 640px) and (max-width: 768px) {
  .promo__container {
    min-height: 400px;
  }
}
.types-goods {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
  font-size: 1.35714286rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
}
.types-goods__item {
  background-color: var(--green);
}
.types-goods__item--back-darker {
  --green: #56c3b2;
  background-color: var(--green);
}
.types-goods__link {
  color: var(--white);
  text-decoration: none;
  display: block;
  padding: 24px 0 31px 30px;
}
.types-goods__link-interior {
  position: relative;
}
.types-goods__link-interior::after {
  content: url("../img/icons/interior.svg");
  position: absolute;
  display: block;
  width: 40px;
  height: 62px;
  top: 50%;
  transform: translateY(-50%);
  right: 36px;
}
.types-goods__link-toy {
  position: relative;
}
.types-goods__link-toy::after {
  content: url("../img/icons/toy.svg");
  position: absolute;
  display: block;
  width: 60px;
  height: 60px;
  top: 50%;
  transform: translateY(-50%);
  right: 27px;
}
.types-goods__span {
  display: block;
  width: 130px;
}
.headline__block {
  margin-bottom: 18px;
}
.headline__title {
  margin: 0;
  font-size: 1.78571429rem;
  line-height: 30px;
}
.headline__paragraph {
  margin: 0;
  line-height: 20px;
  font-size: 1.21428571rem;
  text-transform: uppercase;
  font-weight: bold;
  color: var(--green);
}
.descrip-product__paragraph {
  margin: 0;
  line-height: 24px;
  font-size: 1.07142857rem;
  margin-bottom: 28px;
}
.descrip-product__characteristics {
  margin-bottom: 28px;
}
.characteristics {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  margin: 0;
}
.characteristics__block {
  display: flex;
  padding: 12px 0 14px 0;
  border-bottom: 1px solid var(--gray);
}
.characteristics__title {
  flex-basis: 97px;
}
.characteristics__value {
  flex-grow: 1;
  margin: 0;
}
